import React, { useState } from 'react';
import { View, Text, ActionButton } from '@adobe/react-spectrum';
import ChevronRight from '@spectrum-icons/workflow/ChevronRight';
import ChevronDown from '@spectrum-icons/workflow/ChevronDown';

const NavigationSection = ({ title, children, isExpanded: controlledExpanded }) => {
  const [isExpanded, setIsExpanded] = useState(controlledExpanded !== undefined ? controlledExpanded : true);

  React.useEffect(() => {
    if (controlledExpanded !== undefined) {
      setIsExpanded(controlledExpanded);
    }
  }, [controlledExpanded]);

  return (
    <View
      UNSAFE_style={{
        display: 'flex',
        flexDirection: 'column',
        gap: '0'
      }}
    >
      <style>
        {`
          .spectrum-ActionButton-label {
            text-align: left !important;
          }
          .custom-action-button {
            padding: 8px 0px 8px 5px !important;
          }
        `}
      </style>
      <ActionButton
        isQuiet
        onPress={() => setIsExpanded(!isExpanded)}
        UNSAFE_style={{
          width: '100%',
          justifyContent: 'flex-start',
          padding: '4px 0px 8px 0px'
        }}
      >
        {isExpanded ? <ChevronDown size="S" /> : <ChevronRight size="S" />}
        <Text
          marginStart="size-100"
          UNSAFE_style={{
            fontSize: '14px',
            fontWeight: 'var(--spectrum-global-font-weight-bold)',
            color: 'var(--spectrum-global-color-gray-700)',
            letterSpacing: '0.06em',
            textAlign: 'left'
          }}
        >
          {title}
        </Text>
      </ActionButton>
      {isExpanded && children}
    </View>
  );
};

export default NavigationSection;