import React, { useMemo, useState } from 'react';
import { View, ActionButton, ButtonGroup, Button } from '@adobe/react-spectrum';
import NavigationHeader from './NavigationHeader';
import NavigationSection from './NavigationSection';
import NavigationItem from './NavigationItem';
import Home from '@spectrum-icons/workflow/Home';
import PinOn from '@spectrum-icons/workflow/PinOn';
import Folder from '@spectrum-icons/workflow/Folder';
import TaskList from '@spectrum-icons/workflow/TaskList';
import Calendar from '@spectrum-icons/workflow/Calendar';
import Table from '@spectrum-icons/workflow/Table';
import Document from '@spectrum-icons/workflow/Document';
import FolderOpen from '@spectrum-icons/workflow/FolderOpen';
import GraphTrend from '@spectrum-icons/workflow/GraphTrend';
import Campaign from '@spectrum-icons/workflow/Campaign';
import Project from '@spectrum-icons/workflow/Project';
import Help from '@spectrum-icons/workflow/Help';
import Dashboard from '@spectrum-icons/workflow/Dashboard';
import Report from '@spectrum-icons/workflow/Report';
import User from '@spectrum-icons/workflow/User';
import Group from '@spectrum-icons/workflow/Group';
import Clock from '@spectrum-icons/workflow/Clock';
import UserGroup from '@spectrum-icons/workflow/UserGroup';
import Settings from '@spectrum-icons/workflow/Settings';
import Properties from '@spectrum-icons/workflow/Properties';

const SideNavigation = () => {
  const [isCustomizing, setIsCustomizing] = useState(false);
  const [hiddenItems, setHiddenItems] = useState([]);
  const [isHiddenSectionExpanded, setIsHiddenSectionExpanded] = useState(false);

  const mainItems = useMemo(() => [
    { id: 'home', name: 'Home', icon: Home, path: '/home' },
    { id: 'quick-nav', name: 'My quick navigation', icon: PinOn, path: '/quick-nav' }
  ], []);

  const planningItems = useMemo(() => [
    { id: 'workspaces', name: 'Workspaces', icon: Folder, path: '/workspaces' },
    { id: 'prioritize', name: 'Prioritize', icon: TaskList, path: '/prioritize' },
    { id: 'boards', name: 'Boards', icon: Table, path: '/boards' },
    { id: 'calendars', name: 'Calendars', icon: Calendar, path: '/calendars' },
    { id: 'blueprints', name: 'Blueprints', icon: Document, path: '/blueprints' }
  ], []);

  const workItems = useMemo(() => [
    { id: 'portfolios', name: 'Portfolios', icon: FolderOpen, path: '/portfolios' },
    { id: 'programs', name: 'Programs', icon: GraphTrend, path: '/programs' },
    { id: 'campaigns', name: 'Campaigns', icon: Campaign, path: '/campaigns' },
    { id: 'projects', name: 'Projects', icon: Project, path: '/projects' },
    { id: 'documents', name: 'Documents', icon: Document, path: '/documents' },
    { id: 'requests', name: 'Requests', icon: Help, path: '/requests' }
  ], []);

  const monitoringItems = useMemo(() => [
    { id: 'dashboards', name: 'Dashboards', icon: Dashboard, path: '/dashboards' },
    { id: 'reports', name: 'Reports', icon: Report, path: '/reports' },
    { id: 'analytics', name: 'Analytics', icon: GraphTrend, path: '/analytics' }
  ], []);

  const peopleItems = useMemo(() => [
    { id: 'users', name: 'Users', icon: User, path: '/users' },
    { id: 'teams', name: 'Teams', icon: Group, path: '/teams' },
    { id: 'timesheets', name: 'Timesheets', icon: Clock, path: '/timesheets' },
    { id: 'resourcing', name: 'Resourcing', icon: UserGroup, path: '/resourcing' },
    { id: 'people-calendars', name: 'Calendars', icon: Calendar, path: '/calendars' }
  ], []);

  const toolsItems = useMemo(() => [
    { id: 'setup', name: 'Setup', icon: Settings, path: '/setup' }
  ], []);

  const handleSave = () => {
    // Get all items that are currently hidden
    const allItems = [
      ...planningItems,
      ...workItems,
      ...monitoringItems,
      ...peopleItems,
      ...toolsItems
    ];
    
    // Find items that should be moved back to their original sections
    const itemsToRestore = hiddenItems.filter(item => item.isVisible);
    
    // Remove restored items from hiddenItems
    const remainingHiddenItems = hiddenItems.filter(item => !item.isVisible);
    
    // Find new items that should be hidden
    const newHiddenItems = allItems.filter(item => 
      !item.isVisible && !hiddenItems.some(hidden => hidden.id === item.id)
    );

    setHiddenItems([...remainingHiddenItems, ...newHiddenItems]);
    setIsCustomizing(false);
    setIsHiddenSectionExpanded(false);
  };

  const handleCancel = () => {
    setIsCustomizing(false);
    setIsHiddenSectionExpanded(false);
  };

  const handleVisibilityChange = (itemId, isVisible) => {
    const allItems = [
      ...planningItems,
      ...workItems,
      ...monitoringItems,
      ...peopleItems,
      ...toolsItems
    ];
    
    const item = allItems.find(i => i.id === itemId);
    if (item) {
      item.isVisible = isVisible;
    }
  };

  return (
    <View
      UNSAFE_style={{
        width: '256px',
        height: '100%',
        backgroundColor: 'var(--spectrum-global-color-gray-50)',
        borderRight: '1px solid var(--spectrum-global-color-gray-200)',
        display: 'flex',
        flexDirection: 'column'
      }}
    >
      <NavigationHeader title="Navigation" />
      <View
        UNSAFE_style={{
          flex: 1,
          overflow: 'auto',
          padding: 'var(--spectrum-global-dimension-size-200)',
          gap: 'var(--spectrum-global-dimension-size-200)'
        }}
      >
        <View>
          {mainItems.map(item => (
            <NavigationItem key={item.id} item={item} isCustomizing={isCustomizing} />
          ))}
        </View>

        <View UNSAFE_style={{ marginTop: 'var(--spectrum-global-dimension-size-100)' }}>
          <NavigationSection title="Planning & Strategy">
            <View>
              {planningItems
                .filter(item => !hiddenItems.some(hidden => hidden.id === item.id))
                .map(item => (
                  <NavigationItem 
                    key={item.id} 
                    item={item} 
                    isCustomizing={isCustomizing}
                    onVisibilityChange={handleVisibilityChange}
                  />
                ))}
            </View>
          </NavigationSection>
        </View>

        <View UNSAFE_style={{ marginTop: 'var(--spectrum-global-dimension-size-100)' }}>
          <NavigationSection title="Work Items">
            <View>
              {workItems
                .filter(item => !hiddenItems.some(hidden => hidden.id === item.id))
                .map(item => (
                  <NavigationItem 
                    key={item.id} 
                    item={item} 
                    isCustomizing={isCustomizing}
                    onVisibilityChange={handleVisibilityChange}
                  />
                ))}
            </View>
          </NavigationSection>
        </View>

        <View UNSAFE_style={{ marginTop: 'var(--spectrum-global-dimension-size-100)' }}>
          <NavigationSection title="Monitoring">
            <View>
              {monitoringItems
                .filter(item => !hiddenItems.some(hidden => hidden.id === item.id))
                .map(item => (
                  <NavigationItem 
                    key={item.id} 
                    item={item} 
                    isCustomizing={isCustomizing}
                    onVisibilityChange={handleVisibilityChange}
                  />
                ))}
            </View>
          </NavigationSection>
        </View>

        <View UNSAFE_style={{ marginTop: 'var(--spectrum-global-dimension-size-100)' }}>
          <NavigationSection title="People & Resourcing">
            <View>
              {peopleItems
                .filter(item => !hiddenItems.some(hidden => hidden.id === item.id))
                .map(item => (
                  <NavigationItem 
                    key={item.id} 
                    item={item} 
                    isCustomizing={isCustomizing}
                    onVisibilityChange={handleVisibilityChange}
                  />
                ))}
            </View>
          </NavigationSection>
        </View>

        <View UNSAFE_style={{ marginTop: 'var(--spectrum-global-dimension-size-100)' }}>
          <NavigationSection title="Tools">
            <View>
              {toolsItems
                .filter(item => !hiddenItems.some(hidden => hidden.id === item.id))
                .map(item => (
                  <NavigationItem 
                    key={item.id} 
                    item={item} 
                    isCustomizing={isCustomizing}
                    onVisibilityChange={handleVisibilityChange}
                  />
                ))}
            </View>
          </NavigationSection>
        </View>

        {hiddenItems.length > 0 && (
          <View UNSAFE_style={{ marginTop: 'var(--spectrum-global-dimension-size-100)' }}>
            <NavigationSection 
              title="Hidden items" 
              isExpanded={isHiddenSectionExpanded}
              onToggle={() => setIsHiddenSectionExpanded(!isHiddenSectionExpanded)}
            >
              <View>
                {hiddenItems.map(item => (
                  <NavigationItem 
                    key={item.id} 
                    item={item} 
                    isCustomizing={isCustomizing}
                    onVisibilityChange={handleVisibilityChange}
                    isHiddenItem={true}
                  />
                ))}
              </View>
            </NavigationSection>
          </View>
        )}
      </View>
      <View
        UNSAFE_style={{
          borderTop: '1px solid var(--spectrum-global-color-gray-200)',
          padding: 'var(--spectrum-global-dimension-size-200)',
          backgroundColor: 'var(--spectrum-global-color-gray-50)'
        }}
      >
        {isCustomizing ? (
          <ButtonGroup>
            <Button variant="secondary" onPress={handleCancel}>Cancel</Button>
            <Button variant="accent" onPress={handleSave}>Save</Button>
          </ButtonGroup>
        ) : (
          <ActionButton
            isQuiet
            onPress={() => setIsCustomizing(true)}
            UNSAFE_style={{
              width: '100%',
              justifyContent: 'flex-start'
            }}
          >
            <Properties />
            <View marginStart="size-100">Customize</View>
          </ActionButton>
        )}
      </View>
    </View>
  );
};

export default SideNavigation; 